import React, { Component } from 'react';

class NewWeight extends Component {
    constructor(props) {
        super(props);
        this.state = { w: '' };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({ w: event.target.value });
    }

    handleSubmit(event) {
        var x = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(this.state)
        };
        fetch('http://82.15.239.205:9000/weight', x)
            .then(response => {
                if (!response.ok) {
                    alert('error');
                }
                else {
                    alert('success');
                }
            })
        event.preventDefault();
    }
    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <div className="form-group-sm">
                    <label className="col-sm-2 col-form-label">Weight Info</label>
                </div>
                <div className="form-row align-items-center">
                    <div className="col-auto" style={{ margin: 'auto' }}>
                        <div className="input-group mb-2">
                            <input type="text" className="form-control" name="w" placeholder="Weight" value={this.state.w} onChange={this.handleChange} />
                            <div className="input-group-append">
                                <div className="input-group-text">kg</div>
                            </div>
                        </div>
                    </div>
                </div>
                <input className="btn btn-primary mb-2" type="submit" value="Submit" />
            </form>
        );
    }
}

export default NewWeight;