import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faRunning } from '@fortawesome/free-solid-svg-icons';

class Navigation extends Component {
    state = {}
    render() {
        return (
            <nav className="navbar navbar-dark bg-dark" style={{ paddingLeft: '0.5em', paddingTop: '0em', paddingBottom: '0em' }}>
                <NavLink exact className="navbar-brand" activeClassName="current" to='/'><FontAwesomeIcon icon={faRunning} className="fa-2x" />Workout App</NavLink>
                <NavLink exact activeClassName="current" to='/add'><button className="btn btn-info"><FontAwesomeIcon icon={faPlus} /></button></NavLink>
            </nav>
        );
    }
}

export default Navigation;