import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';

class AddSelect extends Component {
    state = {}
    render() {
        return (
            <nav className="navbar navbar-dark bg-dark nav2" style={{ padding: '0em' }}>
                <NavLink className="width-50" activeClassName="acc" exact to='/add/weight'>Weight</NavLink>
                <NavLink className="width-50" activeClassName="acc" exact to='/add/exercise'>Exercise Session</NavLink>
            </nav>
        );
    }
}

export default AddSelect;