import React, { Component } from 'react';
import AddSelect from './addpages/addSelect';
import AddWindow from './addpages/addWindow';

class Add extends Component {
    state = {}
    render() {
        return (
            <div>
                <h2>Select a window to add</h2>
                <div>
                    <AddSelect />
                    <AddWindow />
                </div>
            </div>
        );
    }
}

export default Add; 