import React, { Component } from 'react';

class Info extends Component {
    constructor(props) {
        super(props);
        this.state = { weight: "", lastEx: "", streak: "" };
    }

    getWeight() {
        fetch('http://82.15.239.205:9000/weight')
            .then(res => res.json())
            .then(res => this.setState({ weight: res[0]["w"] }));
    }
    getLast() {
        fetch('http://82.15.239.205:9000/exercise/last')
            .then(res => res.json())
            .then(res => this.setState({ lastEx: res[0] }));
    }
    getDate() {
        var date = new Date(this.state.lastEx.date);
        return date.toDateString() + ' - ' + date.toLocaleTimeString('en-US');
    }
    getStreak() {

    }

    componentDidMount() {
        this.getWeight();
        this.getLast();
        this.getStreak();
    }
    render() {
        return (
            <div className="col">
                <p>Information</p><hr style={{ width: "80%" }} />
                <p>Weight: {this.state.weight}kg</p>
                <p>Last Exercise: {this.getDate()}</p>
                <p><b>Streak: {this.state.streak}</b></p>
            </div>
        );
    }
}

export default Info;