import React, { Component } from 'react';

class PB extends Component {
    constructor(props) {
        super(props);
        this.state = { apiResp: "" };
    }

    callAPI() {
        fetch('http://82.15.239.205:9000/exercise/pb')
            .then(res => res.json())
            .then(res => this.setState({ apiResp: res }));
    }

    renderPBs() {
        var x = this.state.apiResp;
        var out = ""
        for (var i = 0; i < Math.min(x.length, 4); i++) {
            out += "<p>" + x[i]["_id"] + ": " + x[i]["maxTotalAmount"]["$numberDecimal"] + x[i]["unit"] + "</p>";
        }
        return out
    }

    componentDidMount() {
        this.callAPI();
    }
    render() {
        return (
            <div className="col">
                <p>Personal Bests</p><hr style={{ width: "80%" }} />
                <div dangerouslySetInnerHTML={{ __html: this.renderPBs() }} />
            </div>
        );
    }
}

export default PB;