import React, { Component } from 'react';
import ExerciseSession from '../exercise';

class History extends Component {
    constructor(props) {
        super(props);
        this.state = {
            apiResp: []
        };
    }

    callAPI() {
        fetch('http://82.15.239.205:9000/exercise')
            .then(res => res.json())
            .then(res => this.setState({ apiResp: res }));
    }

    componentDidMount() {
        this.callAPI();
    }
    render() {
        return (
            <div>
                <h1>History</h1>
                {this.state.apiResp.map((session) => (
                    <ExerciseSession
                        key={session._id}
                        session={session}
                    />
                ))}</div>);
    }
}

export default History;