import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';
import Add from './add';
import Home from './home';

class Main extends Component {
    render() {
        return (
            <Switch>
                <Route exact path='/'>
                    <Home />
                </Route>
                <Route path='/add'>
                    <Add />
                </Route>
            </Switch>
        );
    }
}

export default Main;