import React, { Component } from 'react';

class NewExercise extends Component {
    constructor(props) {
        super(props);
        this.state = {
            type: '',
            amount: '',
            unit: '',
            date: ''
        };
        this.dateInput = React.createRef();
        this.timeInput = React.createRef();

        this.datePicker = this.datePicker.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleChange(event) {
        this.setState({ [event.target.name]: event.target.value });
        console.log(this.state);
    }

    handleSubmit(event) {
        var x = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(this.state)
        };
        fetch('http://82.15.239.205:9000/exercise', x)
            .then(response => {
                if (!response.ok) {
                    alert('error');
                }
                else {
                    alert('success');
                }
            })
        event.preventDefault();
    }

    datePicker(event) {
        var date = this.dateInput.current.value;
        var time = this.timeInput.current.value;
        var dt = new Date(date + "T" + time);
        this.setState({ date: dt })
    }
    componentDidMount() {
        this.dateInput.current.value = "2020-01-01";
        this.timeInput.current.value = "00:00";
        this.datePicker();
    }
    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <div className="form-group-sm">
                    <label className="col-sm-2 col-form-label">Exercise Info</label>
                </div>
                <div className="form-row align-items-center">
                    <div className="col-auto" style={{ margin: 'auto' }}>
                        <div className="input-group mb-2">
                            <input type="text" className="form-control" name="type" placeholder="Exercise Type" value={this.state.type} onChange={this.handleChange} />
                        </div>
                    </div>
                </div>
                <div className="form-group-sm">
                    <label className="col-sm-2 col-form-label"></label>
                </div>
                <div className="form-row align-items-center">
                    <div className="col-auto" style={{ marginLeft: 'auto' }}>
                        <div className="input-group mb-2">
                            <input type="text" className="form-control" name="amount" placeholder="Amount" value={this.state.amount} onChange={this.handleChange} />
                        </div>
                    </div>
                    <div className="col-auto" style={{ marginRight: 'auto' }}>
                        <div className="input-group mb-2">
                            <input type="text" className="form-control" name="unit" placeholder="Unit" value={this.state.unit} onChange={this.handleChange} />
                        </div>
                    </div>
                </div>
                <div className="form-group-sm">
                    <label className="col-sm-2 col-form-label">Date and Time</label>
                </div>
                <div className="form-row align-items-center">
                    <div className="col-auto" style={{ marginLeft: 'auto' }}>
                        <div className="input-group mb-2">
                            <input type='date' className="form-control" name="date" ref={this.dateInput} onChange={this.datePicker} />
                        </div>
                    </div>
                    <div className="col-auto" style={{ marginRight: 'auto' }}>
                        <div className="input-group mb-2">
                            <div className="input-group-prepend">
                                <div className="input-group-text">@</div>
                            </div>
                            <input type='time' className="form-control" name="time" ref={this.timeInput} onChange={this.datePicker} />
                        </div>
                    </div>
                </div>
                <input className="btn btn-primary mb-2" type="submit" value="Submit" />
            </form>
        );
    }
}

export default NewExercise;