import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBiking, faRunning } from '@fortawesome/free-solid-svg-icons';

class ExerciseSession extends Component {
    state = {
        sporttypes: {
            "push-up": <img src="push2.svg" alt="push-up" width="80" style={{ margin: '0 8px' }} />,
            "plank": <img src="plank2.svg" alt="plank" width="80" style={{ margin: '0 8px' }} />,
            "bike-ride": <FontAwesomeIcon icon={faBiking} className="fa-4x" style={{ margin: '8px' }} />,
            "run": <FontAwesomeIcon icon={faRunning} className="fa-4x" style={{ margin: '8px' }} />
        }
    }

    getType() {
        var itemType = this.props.session.type;
        return this.state.sporttypes[itemType]
    }

    getDate() {
        var date = new Date(this.props.session.date);
        return date.toDateString() + ' - ' + date.toLocaleTimeString('en-US');
    }

    render() {
        return (
            <div style={{ display: 'flex', width: '60%', margin: 'auto', border: '1px outset black', borderCollapse: 'collapse' }}>
                <div className="typeSec">
                    {this.getType()}
                </div>
                <div style={{ borderLeft: '1px solid #dbdbdb', marginRight: '50px' }}></div>
                <div className="InfoSec">
                    <p>Date: {this.getDate()}</p>
                    <p>{this.props.session.amount} {this.props.session.unit}</p>
                </div>
            </div>
        );
    }
}

export default ExerciseSession;